import { handleActions } from "redux-actions";
import * as TYPES from "./actionTypes";
//
export const orderList = handleActions(
    {
        [TYPES.ORDER_LIST](state, action) {
            // 
            // const orderListOfState = action.payload;
            // return { ...state, orderListOfState };
            const orderList = action.payload;
            return { ...state, orderList };

        },
        [TYPES.PAGE_SIZE](state, action) {
            //
            const totalSize = action.payload;
            return { ...state, totalSize };
        }
    },
    {
        //,
        orderListOfState: {
            0: [], //
            1: [], //
            2: [], //
            3: [], //
            4: [], //
            5: [], //
            6: [] //
        },
        orderList:[],
        stateMap: {
            1: "",
            2: "",
            3: "",
            4: "",
            5: "",
            6: ""
        },
        stateBtntextMap: {
            1: "",
            4: ""
        },
        totalSize:0,//
    }
);

export default orderList;
