// 
import axios from "axios";
import { appCode, appKey,apiMaps ,env} from "./config.js";
import { formatUrl } from "../util/util.js";
import { message } from "antd";

const getToken = () => {
    const data = { appkey: appKey };
    return axios({
        url: "auth/getToken",
        method: "post",
        data
    });
};

const params = formatUrl(); //params.channelCode id
let channelCode = params.channelCode || "newhks";

//
const CancelToken = axios.CancelToken
let pending = []
const removePending = (config,cancel)=>{
    for (let i = 0; i < pending.length; i++) {
        const ajaxObj = pending[i];
        if(ajaxObj.id==`${config.url}&${config.method}`){
            cancel&&ajaxObj.c()
            pending.splice(i,1)
            return 
        }
    }
}

let token = "test";
axios.defaults.retry = 4;
axios.defaults.retryDelay = 1000;
// axios.defaults.withCredentials = true;//cookie
axios.interceptors.request.use(
    function(config) {
        //thirdInterfacetrue
        if (!config.thirdInterface) {
            config.source = config.source||'tianJun'//baseUrl
            const api = apiMaps[config.source][env] 
            config.params = config.params ? config.params : {};
            config.baseURL = api;
            config.params.appcode = appCode;
            config.params.token = token;
            config.params.timestamp = new Date().getTime();
            config.params.channelCode = channelCode;
        }else{
            config.baseURL = 'http://121.42.248.165:13003/';
            config.params = config.params ? config.params : {};
        }
        //
        // removePending(config,true) //,
        config.cancelToken = new CancelToken(c=>{
            pending.push({
                id:`${config.url}&${config.method}`,
                c
            })
        })
        
        return config;
    },
    function(error) {
        return Promise.reject(error);
    }
);

axios.interceptors.response.use(
    function(response) {
        if(response.config.thirdInterface){ //
            if(response.data.success){
                return response.data
            }else{
                message.error(response.data.msg)
            }
        } else { //
            //token
            if (response.data.errcode == 1004) {
                let config = response.config;
                // token
                return getToken().then(res => {
                    token = res.data.token;
                    return axios(config);
                });
            } else if (response.data.errcode != 0 && !response.config.noErr) {
                //noErrtrue
                message.error(response.data.errmsg);
            }
            // removePending(response.config) //,
            return response;
        }
    },
    function(error) {
        return Promise.reject(error);
    }
);

export { axios };
