import React, { Component } from "react";
import { Radio, Icon, Modal, Input, Button, Cascader, message } from "antd";
import { connect } from "react-redux";
import moment from "moment";
import { getaddressList, delAddress,addAddressList,delAddressList,updateAddressList } from "../actions";
import "./index.scss";
import CompanyItem from "./components/Company.js";
import { getArea, orderGenerate } from "../api.js";
import valihks from "util/validate.js";
@connect(
    state => ({
        ...state.Order_reducer,
        userinfo: state.Login_reducer.hks_userInfo
    }),
    { getaddressList, delAddress,addAddressList,delAddressList,updateAddressList }
)
class Order extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            isAdd: true,
            isShowMore: false, //
            options: [], //
            radioIndex: 0,
            addressInfo: {
                consignee: "", //
                areaID: "",
                areaName: "",
                mobile: "",
                address: ""
            },
            defaultValue: [], //
            purchaseDescribeObj: [
                // {supplie:'',supplieName:'',content:''}
            ] //
        };
    }
    componentDidMount() {
        // const data = {
        //     accountName: this.props.userinfo.customeraccount.accountName
        // };
        this.props.getaddressList();
        this.getAreaData(0, data => {
            this.setState({
                options: data
            });
        });

        const { goodsList } = this.props;
        console.log("**************");
        console.log("goodsList", goodsList);
        console.log("**************");

        let purchaseDescribeObj = goodsList.map(v => ({
            supplie: v.supplie,
            supplieName: v.supplieName,
            content: ""
        }));
        this.setState({
            purchaseDescribeObj
        });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.goodsList != this.props.goodsList) {
            console.log("**************");
            console.log("nextProps.goodsList", nextProps.goodsList);
            console.log("**************");
            let purchaseDescribeObj = nextProps.goodsList.map(v => ({
                supplie: v.supplie,
                supplieName: v.supplieName,
                content: ""
            }));
            this.setState({
                purchaseDescribeObj
            });
        }
    }
    //
    handlePurchaseDescribe = (index, e) => {
        this.setState({
            purchaseDescribeObj: this.state.purchaseDescribeObj.map((v, i) => {
                if (i == index) {
                    return {
                        supplie: v.supplie,
                        supplieName: v.supplieName,
                        content: e.target.value.trim()
                    };
                }
                return v;
            })
        });
    };
    radioChange = e => {
        const value = e.target.value;
        this.setState({
            radioIndex: value
        });
    };
    getAreaData = (pid, callBack) => {
        getArea({
            areaPrice: {
                appkey: "1",
                pid
            }
        }).then(res => {
            if (res.data.errcode == 0) {
                const data = res.data.areaPriceList,
                    arr = [];
                for (let index = 0; index < data.length; index++) {
                    const element = data[index];
                    if (
                        !element.name.includes("") &&
                        element.name != "" &&
                        element.name != "" &&
                        element.name != ""
                    ) {
                        let obj = {
                            id: element.id,
                            name: element.name
                        };
                        if (element.areaLevel < 3) {
                            obj.isLeaf = false;
                        }
                        arr.push(obj);
                    }
                }
                callBack.call(this, arr);
            }
        });
    };
    areaChange = (value, selectedOptions) => {
        console.log(this.state.options);
        if (value && value.length == 3) {
            let areaName = "";
            const areaID = value[2];
            selectedOptions.forEach(val => {
                areaName += `${val.name} `;
            });
            areaName = areaName.slice(0, areaName.length - 1);

            this.setState({
                addressInfo: { ...this.state.addressInfo, areaName, areaID }
            });
        }
    };
    loadData = selectedOptions => {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;
        const { id } = targetOption;
        this.getAreaData(id, data => {
            targetOption.loading = false;
            targetOption.children = data;
            this.setState(preState => {
                return {
                    options: [...preState.options]
                };
            });
        });
    };
    displayRender=(label, selectedOptions)=>{
        const {defaultValue}= this.state

        if(defaultValue.length){
                const province = this.findAreaById(defaultValue[0])
                const city = this.findAreaById(defaultValue[1])
                const area = this.findAreaById(defaultValue[2])
                return `${province?province.name:''}/${city?city.name:''}/${area?area.name:''}`
        }
        
    }
    closeDialog = () => {
        this.setState({
            visible: false
        });
    };
    //
    showAddDialog = () => {
        this.setState({
            isAdd: true,
            visible: true,
            addressInfo: {
                consignee: "", //
                areaID: "",
                areaName: "",
                mobile: "",
                address: ""
            },
            defaultValue:[]
        });
    };
    findAreaById = id => {
        const { options } = this.state;
        id = String(id);
        if(id){
            switch (id.length) {
                case 2:
                    return options&&options.find(v => v.id == id);
                case 4:
                    const provinceID = id.slice(0, 2);
                    const province = options&&options.find(v => v.id == provinceID);
                    const city = province&&province.children&&province.children.find(v => v.id == id);
                    return city;
                case 6:
                    const provinceID2 = id.slice(0, 2);
                    const cityID = id.slice(0, 4);
                    const province2 =options&&options.find(v => v.id == provinceID2);
                    const city2 = province2&&province2.children&&province2.children.find(v => v.id == cityID);
                    const area = city2&&city2.children&&city2.children.find(v => v.id == id);
                    return area;
    
                default:
                    break;
            }
        }else{
            return null
        }
        
    };
    //
    showEditDialog = address => {
        console.log("address", address);
        const areaIdStr = address.areaID.toString();
        const defaultValue = [
            Number(areaIdStr.slice(0, 2)),
            Number(areaIdStr.slice(0, 4)),
            Number(areaIdStr.slice(0, 6))
        ];
        this.getAreaData(defaultValue[0], data => {
            //options
            
            this.findAreaById(defaultValue[0]).children = data
            // this.setState(preState => {
            //     return {
            //         options: [...preState.options]
            //     };
            // });
            
            this.getAreaData(defaultValue[1], data => {
                const area = this.findAreaById(defaultValue[1])
                area.children = data
                this.setState(preState => {
                    console.log(111,area,preState.options,preState.defaultValue);
                    return {
                        options: JSON.parse(JSON.stringify(preState.options)),//[...preState.options]
                        
                        
                    };
                },()=>{
                    
                    console.log(22,this.state.options);
                    
                });
                this.forceUpdate()
                
                
            });
        });
       
        this.setState({
            isAdd: false,
            visible: true,
            addressInfo: address,
            defaultValue
        });
    };
    //
    saveAddress = () => {
        const vali = valihks();
        const { addressInfo } = this.state;
        const { areaID, consignee, address, mobile,areaName } = addressInfo;
        vali.add({ val: areaID, msg: "", type: "required" });
        vali.add({ val: consignee, msg: "", type: "required" });
        vali.add({ val: address, msg: "", type: "required" });
        vali.add({
            val: mobile,
            msg: "",
            type: "required"
        });
        const msg = vali.vali();
        if (msg) {
            message.warning(msg);
            return;
        }
        const { addAddressList ,updateAddressList,userinfo} = this.props;
        const data  ={
            consigneeofcustomer:{
                accountName:userinfo.customeraccount.accountName,
                areaID,
                areaName,
                address,
                consignee,
                mobile,
                isDefault:1
            }
        }
        //
        if(this.state.isAdd){
            addAddressList(data)
        }else{
            //
            console.log(addressInfo);
            
            data.consigneeofcustomer.consigneeOfCustomerID =addressInfo.consigneeOfCustomerID
            updateAddressList(data)
        }
        this.setState({ visible: false });
    };
    handleChange = e => {
        const value = e.target.value.trim();
        this.setState({
            addressInfo: { ...this.state.addressInfo, [e.target.name]: value }
        });
    };

    showMore = () => {
        this.setState({
            isShowMore: true
        });
    };
    showLess = () => {
        this.setState({
            isShowMore: false
        });
    };
    delAddress = index => {
        const me = this;
        const confirm = Modal.confirm;
        
        confirm({
            title: "",
            okType: "danger",
            onOk() {
                const {addressList,delAddressList,userinfo}  = me.props
                const data = {
                    consigneeofcustomer:{

                        accountName:userinfo.customeraccount.accountName,
                        consigneeOfCustomerID:addressList[index].consigneeOfCustomerID
                    }
                }
                delAddressList(data)
                // me.props.delAddress(index);
            }
        });
    };

    //
    toPay = () => {
        //
        //partTerminologyID7577storeNumberck001
        const { radioIndex, purchaseDescribeObj } = this.state;
        const { addressList, userinfo, goodsList, total } = this.props;
        const showAddressInfo = addressList[radioIndex];
        console.log("^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^");
        console.log(goodsList);
        console.log("^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^");
        const purchaseDescribe = purchaseDescribeObj
            .map(v => {
                return `${v.supplieName}:${v.content}`;
            })
            .join(";");
        // 
        // 
        // 12345 17
        // 718
        // 8()19
        const parttopurchases = [];
        const hksSelf = [1, 2, 3, 4, 5];
        goodsList.forEach(val => {
            val.partItems &&
                val.partItems.forEach(part => {
                    const amount = part.price * part.quantity; //
                    let purchaseOrderSource, warehouseType; //
                    // 
                    if (part.inventory && part.inventory.length) {
                        for (let i = 0; i < part.inventory.length; i++) {
                            const inv = part.inventory[i];
                            if (inv.warehouseType) {
                                warehouseType = inv.warehouseType;
                                if (hksSelf.indexOf(inv.warehouseType) > -1) {
                                    purchaseOrderSource = 17;
                                    return;
                                }
                                if (inv.warehouseType == 7) {
                                    purchaseOrderSource = 18;
                                    return;
                                }
                                if (inv.warehouseType == 8) {
                                    purchaseOrderSource = 19;
                                    return;
                                }
                            }
                        }
                    } else if (part.source) {
                        purchaseOrderSource = 19;
                    }

                    let storeNumber;
                    if (part.inventory && part.inventory.length) {
                        storeNumber = part.inventory.find(
                            inv => inv.storenumber
                        ).storenumber;
                    }

                    const partInfo = {
                        activityID: "", //ID
                        setMealToActivityID: "", //ID
                        partItemID: part.partItemID, //ID
                        partNumber: part.partNumber, //
                        partTerminologyID:
                            part.partItem.partterminologyid || 7577, //ID,
                        partTerminologyName: part.partItem.partterminologyname, //
                        partBrandID: part.partItem.partbrandid, //ID
                        partBrandName: part.partItem.partbrandname, //
                        partSubBrandID: "", //ID
                        partSize: part.partSize, //
                        partSizeUOM: part.partSizeUOM, //
                        containerUOM: part.containerUOM, // TODO:
                        partQuantity: part.quantity, //
                        hKSCheckPrice: "", //
                        price: part.price, //
                        priceDiscount: "", //
                        priceReal: part.price, //
                        amountDiscount: 0, //
                        amountReal: amount, //
                        directPrice: "", //
                        supplieID: val.supplie, //ID
                        storeNumber: storeNumber || "ck001", //
                        positionNumber: "", //
                        warehouseType: warehouseType || 8, //1- 2-3-4-5- 6- 7- 8-
                        settlementState: 0, //0-1-
                        amount, //
                        useNumber: "", //
                        manHours: "", //
                        manHourAmount: "", //
                        cid: "", //ID
                        score: "", //
                        partName: part.partName, //
                        purchaseOrderSource: purchaseOrderSource, //1-2-3-OTO 17--- 18--&-- 19--&--
                        multiple: 0, //0-1-
                        isNeedInvoice: part.isNeedInvoice, // 0  1 
                        freightPaymentMethod: part.freightPaymentMethod, // 1  2 
                        partQuality: "" //
                    };
                    parttopurchases.push(partInfo);
                });
        });
        //TODO: 
        const data = {
            //
            purchaseOrder: {
                channelID: "", //ID
                vehicleID: "", //ID
                customerID: userinfo.customer.customerID, //ID
                customerName: userinfo.customer.customerName, //
                customerMobile: userinfo.customer.mobile, //
                operatorID: userinfo.customeraccount.customerAccountID, //IDIDcustomerAccountID
                operatorName: userinfo.customeraccount.accountName, //accountName
                storeCode: "hks", //
                storeName: "", //
                createTime: moment().format("YYYY-MM-DD HH:mm:ss"), //
                createOperator: userinfo.customeraccount.accountName, //
                purchaseOrderState: 1, //1-0-1
                paymentState: 0, //1-0-
                consignee: showAddressInfo.consignee, //),
                consigneeMobile: showAddressInfo.mobile, //
                deliveryAddress: showAddressInfo.address, //
                areaID: showAddressInfo.areaID, //
                zipCode: "", //,
                purchaseDescribe, //, //TODO:
                paymentMethod: "", //1-2-3-4-
                paymentTime: "", // datetime
                purchaseAmount: total, //, TODO:
                shippingMethod: "3", //1 - 2-  3 -TODO:
                freight: "", //, TODO:
                sourceOrderNumber: "", //
                cid: "", //ID
                carNumber: "", //,TODO:
                vINCode: "", //VINTODO:
                hKSCode: "", //ID TODO:
                vehicleDescribe: "", //  TODO:
                purchaseAmountOriginal: total, //,TODO:
                wayTime: "", //yyyy-MM-ddTODO:
                mileage: "", //kmTODO:
                purchaseOrderSource: parttopurchases[0].purchaseOrderSource, //1-2-3-OTO4- 7- 8- 17---18--&--19--&--
                freightPaymentMethod: "", // 1  2 1 ; 2 
                state: 1 //1-2-3-4-5-6-
            },
            //parttopurchase
            parttopurchases,
            // [
            //     {
            //         activityID: "", //ID
            //         setMealToActivityID: "", //ID
            //         partItemID: "", //ID
            //         partNumber: "", //
            //         partTerminologyID: "", //ID,
            //         partTerminologyName: "", //
            //         partBrandID: "", //ID
            //         partBrandName: "", //
            //         partSubBrandID: "", //ID
            //         partSize: "", //
            //         partSizeUOM: "", //
            //         containerUOM: "", //
            //         partQuantity: "", //
            //         hKSCheckPrice: "", //
            //         price: "", //
            //         priceDiscount: "", //
            //         priceReal: "", //
            //         amountDiscount: "", //
            //         amountReal: "", //
            //         directPrice: "", //
            //         supplieID: "", //ID
            //         storeNumber: "", //
            //         positionNumber: "", //
            //         warehouseType: "", //1- 2-3-4-5- 6- 7- 8-
            //         settlementState: 0, //0-1-
            //         amount: "", //
            //         useNumber: "", //
            //         manHours: "", //
            //         manHourAmount: "", //
            //         cid: "", //ID
            //         score: "", //
            //         partName: "", //
            //         purchaseOrderSource: "", //1-2-3-OTO
            //         multiple: 0, //0-1-
            //         isNeedInvoice: "", // 0  1 
            //         freightPaymentMethod: "", // 1  2 
            //         partQuality: "" //
            //     }
            // ],
            // //manhourtopurchase
            // manhourtopurchases: [
            //     {
            //         activityID: "", //ID
            //         setMealToActivityID: "", //ID
            //         manHourID: "", //ID
            //         manHourName: "", //
            //         manHourUnit: "", //
            //         manHourQuantity: "", //
            //         manHourAmount: "", //
            //         modelBrandName: "", //
            //         hKSCheckPrice: "", //
            //         manHourPrice: "", //
            //         manHourPriceDiscount: "", //
            //         manHourPriceReal: "", //
            //         manHourAmountDiscount: "", //
            //         manHourAmountReal: "", //
            //         settlementState: "", //0-1-
            //         manHours: "", //
            //         useNumber: "", //
            //         cid: "", //ID
            //         score: "", //
            //         supplieID: "", //ID
            //         purchaseOrderSource: "" //1-2-3-OTO
            //     }
            // ],
            //
            // purchaseOrderAttribute: {
            //     consigneeState: "", //o2o0-1-
            //     outOfStock: "", //	0-1-
            //     notInPart: "", //	0-1-
            //     workOrderFlag: "", //	0-1-
            //     storeBalanceFlag: "", //	0-1-
            //     channelBalanceFlag: "", //	0-1-
            //     operatorName: "", //	
            //     isScore: "" //	0-1-
            // },
            //
            freightToPurchase: {
                freightOrigin: 0, //
                packingFee: 0, //		
                urgentFee: 0, //	
                freightDiscount: 0, //	
                freightReal: 0 //	
            },
            //
            // deliverytopurchase: {
            //     expressNumber: "", //	
            //     expressCompay: "", //	
            //     sender: "", //	
            //     sendTime: "", //		
            //     senderMobile: "", //	
            //     sendDescribe: "", //		
            //     consignee: "", //		
            //     deliveryTime: "", //		
            //     deliveryDescribe: "", //		
            //     supplieID: "", //	ID
            //     storeNumber: "", //	
            //     consigneeState: "", //	
            //     positionNumber: "", //	
            //     cid: "" //		ID
            // },
            //
            // paymenttopurchase: {
            //     tradeNumber: "", //	
            //     paymentType: "", //	
            //     payTime: "", //	
            //     payState: "" //	
            // },
            checkInven: false,
            timestamp: new Date().getTime()
        };

        orderGenerate(data).then(res => {
            if (res.data.errcode == 0) {
                this.props.history.push(
                    `/cashier/${res.data.purchaseOrderNumbers[0]}`
                );
            }
        });
        // goodsList.flatMap((val)=>)       //TODO: 
    };

    render() {
        const {
            visible,
            isShowMore,
            options,
            isAdd,
            radioIndex,
            purchaseDescribeObj,
            defaultValue
        } = this.state;
        console.log('defaultValue',defaultValue);
        
        const { consignee, address, mobile, areaID } = this.state.addressInfo;
        const { addressList, goodsList, total } = this.props;

        const RadioGroup = Radio.Group;
        const title = isAdd ? "" : "";
        const showAddressInfo = addressList[radioIndex];
        let addressSpans;
        if (showAddressInfo) {
            addressSpans = [
                ...showAddressInfo.areaName.split(" "),
                showAddressInfo.address
            ].map(v => (
                <span className="mr5" key={v}>
                    {v}
                </span>
            ));
        }
        // let addressSpans =[... showAddressInfo.areaName.split(' '),showAddressInfo.address].map(v=>(<span className="mr5">{v}</span>))

        const addressItems = addressList.map((address, index) => {
            let [provice, city, area] = address.areaName.split(" ");
            return (
                <p className="address-item" key={index}>
                    <Radio value={index} />
                    <span className="mr5">{provice}</span>
                    <span className="mr5">{city}</span>
                    <span className="mr5">{area}</span>
                    <span className="mr5">{address.address}</span>
                    {/* <span className="mr5">1234</span>
                    <span className="mr5">xxx</span> */}
                    <span className="mr5">({address.consignee})</span>
                    <span className="mr5">{address.mobile}</span>
                    <span className="fr address-operation">
                        <span
                            className="edit cursor"
                            style={{ color: "#7db9fe" }}
                            onClick={() => {
                                this.showEditDialog(address);
                            }}
                        >
                            <Icon type="edit" className="mr8" />
                            
                        </span>
                        <span
                            className="del cursor"
                            onClick={() => {
                                this.delAddress(index);
                            }}
                        >
                            <Icon type="delete" className="mr8" />
                            
                        </span>
                    </span>
                </p>
            );
        });
        const showMore = isShowMore ? (
            <p className="clearfix">
                <Icon
                    type="up"
                    className="fr showLess cursor"
                    onClick={this.showLess}
                />
                
            </p>
        ) : (
            <p onClick={this.showMore} className="cursor">
                
                {/* <i className="">>></i> */}
                <svg className="icon icon-xiala" aria-hidden="true">
                    <use xlinkHref='#icon-xiala' />
                </svg>
            </p>
        );
        const CompanyItems = goodsList.map((v, i) => {
            return (
                <CompanyItem
                    key={i}
                    companyInfo={v}
                    handlePurchaseDescribe={this.handlePurchaseDescribe}
                    index={i}
                    purchaseDescribe={
                        purchaseDescribeObj &&
                        purchaseDescribeObj[i] &&
                        purchaseDescribeObj[i].content
                    }
                />
            );
        });
        return (
            <div className="order container">
                <div className="adress">
                    <h3>
                        
                        <span
                            className="fr addAddress"
                            onClick={this.showAddDialog}
                        >
                            
                        </span>
                    </h3>
                    <div className="address-content">
                        <div
                            className={
                                !isShowMore
                                    ? `address-items`
                                    : `more address-items`
                            }
                        >
                            <RadioGroup
                                value={radioIndex}
                                onChange={this.radioChange}
                            >
                                {addressItems}
                            </RadioGroup>
                        </div>
                        {showMore}
                        {/* <p>
                            <i className="">>></i>
                        </p> */}
                    </div>
                </div>
                <div className="partsInfo">
                    <h3></h3>
                    <div className="partsInfo-content">
                        <div className="partsInfo-con-header">
                            <span className="part"></span>
                            <span className="price"></span>
                            <span className="count"></span>
                            <span className="unit"></span>
                            <span className="amount"></span>
                        </div>
                        {CompanyItems}
                    </div>
                </div>
                <div className="order-footer">
                    <div className="first">
                        <p>
                            
                            {/* <span className="mr5"></span>
                            <span className="mr5"></span>
                            <span className="mr5"></span>
                            <span className="mr5"></span>
                            <span className="mr5">1234</span>
                            <span className="mr5">xxx</span> */}
                            {addressSpans}
                        </p>
                        <p>
                            
                            <span className="mr5">
                                {showAddressInfo && showAddressInfo.consignee}
                            </span>
                            <span className="mr5">
                                {showAddressInfo && showAddressInfo.mobile}
                            </span>
                        </p>
                    </div>
                    <div className="second">
                        <strong></strong>
                        <strong className="warn totalMount">{total}</strong>
                        <button onClick={this.toPay}></button>
                    </div>
                </div>
                <Modal
                    onCancel={this.closeDialog}
                    visible={visible}
                    title={title}
                    className="dialog"
                    footer={null}
                    // onOk={this.handleOk}
                    // onCancel={this.handleCancel}
                >
                    <div>
                        <label>
                            <span className="label dib">
                                <span className="warn">*</span>
                            </span>
                            <Cascader
                            displayRender={this.displayRender}
                                defaultValue={defaultValue}
                                className="address-cascader"
                                fieldNames={{
                                    label: "name",
                                    value: "id",
                                    children: "children"
                                }}
                                options={options}
                                placeholder=""
                                changeOnSelect={true}
                                onChange={this.areaChange}
                                loadData={this.loadData}
                            />
                        </label>
                    </div>
                    <div>
                        <label>
                            <span className="label dib">
                                <span className="warn">*</span>
                            </span>
                            <Input
                                name="consignee"
                                onChange={this.handleChange}
                                value={consignee}
                            />
                        </label>
                    </div>
                    <div>
                        <label>
                            <span className="label dib">
                                <span className="warn">*</span>
                            </span>
                            <Input
                                name="address"
                                onChange={this.handleChange}
                                value={address}
                            />
                        </label>
                    </div>
                    <div>
                        <label>
                            <span className="label dib">
                                <span className="warn">*</span>
                            </span>
                            <Input
                                name="mobile"
                                onChange={this.handleChange}
                                value={mobile}
                            />
                        </label>
                    </div>
                    <div className="btn">
                        <Button onClick={this.saveAddress}>
                            
                        </Button>
                    </div>
                </Modal>
            </div>
        );
    }
}

export default Order;
