import axios from "axios";
//
export const getArea = data => {
    return axios({
        url: "auth/areaPrice/query",
        method: "post",
        data
    }).then(res => res, err => err);
};

//
export const getInventory = data => {
    return axios({
        url: "auth/datastore/queryInventory",
        method: "post",
        data
    }).then(res => res, err => err);
};

