import axios from 'axios'


//
export const getOrder = (data) => {
    return axios({
        url: 'auth/order/queryNewHks',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//
export const getOrderPage = (data) => {
    return axios({
        url: 'auth/order/queryPageNewHks',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//
export const getReturnGoods = (data)=>{
    return axios({
        url: 'auth/returngoods/query',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//
export const getReturnGoodDetail = (data)=>{
    return axios({
        url: 'auth/returngoods/detail',
        method: 'post',
        data
    }).then(res => res, err => err)
}

