import { handleActions } from "redux-actions";
import * as TYPES from "./actionTypes";
//
 const pay = handleActions(
    {
        [TYPES.PAY_LIST](state, action) {
            //
            const payList = action.payload;
            return {...state, payList:payList};
        },
        [TYPES.ORDER_DETAIL](state, action) {
            //
            const orderDetail = action.payload;
            return {...state, orderDetail};
        },
    },
    {
        payList:[],//
        orderDetail:{//
            parttopurchase:{},
            purchaseorder:{},
            manHourToPurchase:{},
            deliverytopurchase:{},
            vehicle:{},
        }
    }

);



export default pay 